import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    musicInfoPath: null, //
    musicCommentPath: null, //
    musicianName: null, //->

    DjRecommendType: {}, // 
    DjRadioHot: {}, //

    frequency: [],

    playListPageCat: {},//

    currentOffset: 1,
    
    mainNav: false,//
    mainNavFlag: false,
    mainNavIndex: false,//


    total: 0,
    key: '',

    policyTip: true,//
    policyTipShow: false,//
    loginShow: false, //
    registerShow: false,//
    userPhone: localStorage.getItem('userPhone'),
    userPassword: localStorage.getItem('userPassword'),
    userToken: '',
    userId: '',
    userTracks: 0,
    userPlayList: [],
    userPageFlag: false,//flag
    userAvatarUrl: '',
    userAutoLogin: localStorage.getItem('userAutoLogin'),//
    userDelTrack: 0,
    tipShow: false,//
    createShow: false,//
    createFlag: false,//
    createName: '',//
    operateShow: false,//
    operateFlag: false,//
    operateInfo: '',
    windowHead: '',
    


    searchIndex: 1,
    searchCurrentCatName: 'singer',//

    requestType: 'playlist',

    artistCatCurrntIndex: 0, //

    type: '', //
    area: '', //

    nType: 0, //
    nArea: 0,

    artistList: {}, //

    singerHotSongs: [],//

    descIsOpen: false,//

    allAlbumInfo: [], //
    albumHotInfo: {}, //
    albumCatInfo: {}, //

    userRecordType: 1,//
    
    windowShow: false,//

    offsetNum: 0,//

    musicIsCompleteUpdate: true,//
    
    
    musicPlayListCatName: '',//
    musicTopListCatName: 0,//

    musicLyricIsPlay: false,//

    musicAudioPlayStatus: false,

    musicAudioIndex: 0,// 

    musicAudioAddAllFlag: false,//flag

    musicAudioId: 0,
    musicAudioIdList:[],

    musicAudioUrl: '',
    musicAudioUrlList: [],

    musicAudioDetail: {},
    musicAudioDetailList: [],

    musicAudioLyric: {},
    musicAudioLyricList: [],

    musicAudioList: []

  },
  getters: {
    // userPhone(){
    //   return localStorage.getItem('userPhone')
    // },
    // userPassword(){
    //   return localStorage.getItem('userPassword')
    // },
    // userAutoLogin(){
    //   return localStorage.getItem('userAutoLogin')
    // },
    // policyTip(){
    //   return localStorage.getItem('policyTip')
    // }
  },
  mutations: {
    changeValue(state, payload) {
      if (Array.isArray(payload)) {
        payload.forEach(item => {
          state[item.type] = item.value
        })
      } else {
        state[payload.type] = payload.value
      }
   
    },
    addValue(state, payload) {
      if (Array.isArray(payload)) {
        payload.forEach(item => {
          state[item.type].push(item.value)
        })
      } else {
        state[payload.type].push(payload.value)
      }

    },
  },
  actions: {},
  modules: {}
})